<template>
	<div
		v-for="(item, order) in this.field_data.enum"
		:class="['form-check', radio_layout]"
	>
		<input
			:name="field_id"
			:id="radio_id(order)"
			v-model="value"
			:value="order"
			:disabled="field_data.disabled"
			type="radio"
			class="form-check-input"
		>
		<label
			:for="radio_id(order)"
			class="form-check-label"
		>
			{{ label(item) }}
		</label>
	</div>
</template>

<script>
	import STATE_params from '@/store/STATE_params.js';
	import STATE_core from '@/store/STATE_core.js';

	export default {
		props: {
			field_id: String,
			field_data: Object,
		},
		components: {
		},
		emits: [],
		data() {
			return {
				combined: {
					// radio_id: select_id,
					auto_typ_2: 'auto_typ',
					havarijne_auto_typ2: 'havarijne_auto_typ',
					cestovne_uzemna_platnost: 'cestovne_krajina',
				},
			}
		},
		created() {
			this.change_combined_input();
		},
		methods: {
			radio_id(order)
			{
				return this.field_id + '-' + order;
			},
			label(item)
			{
				for (var key in item) {
					if (! item.hasOwnProperty(key)) { continue; }
					return item[key];
				}
			},
			change_combined_input()
			{
				let pairs = {
					// group_id: {
					// 	select_value: radio_value,
					// },
					'1': {
						'1': '1',
						'20': '2',
						'33': '3',
					},
					'2': {
						'1': '1',
						'2': '2',
						'20': '3',
					},
					'7': {
						'51': '1',
						'210': '2',
					},
				};
				let group = STATE_core().product_group_id();
				if (pairs[group]?.[this.cached_dynamic_combo] === this.value || this.cached_dynamic_combo === '') {
					return;
				}
				this.value = pairs[group]?.[this.cached_dynamic_combo];
			},
		},
		computed: {
			radio_layout()
			{
				if (
					this.field_data.type_settings === null ||
					this.field_data.type_settings === undefined ||
					this.field_data.type_settings.radio_layout === 'horizontal'
				) {
					return 'form-check-inline';
				} else if (this.field_data.type_settings.radio_layout === 'icons') {
					return 'input-button-icon';
				} else if (this.field_data.type_settings.radio_layout === 'vertical') {
					return '';
				} else if (this.field_data.type_settings.radio_layout === 'blank') {
					return 'input-button-blank';
				} else {
					return 'form-check-inline';
				}
			},
			cached_dynamic_combo()
			{
				if (!this.combined.hasOwnProperty(this.field_id)) {
					return '';
				}
				return STATE_params().get_form_value(this.combined[this.field_id]);
			},
			value:
			{
				get() {
					return STATE_params().get_form_value(this.field_id);
				},
				set(event_value) {
					STATE_params().set_form_value(this.field_id, event_value);
				},
			},
		},
		watch: {
			cached_dynamic_combo()
			{
				this.change_combined_input();
			},
		},
	}
</script>
